<template>
  <v-row>
    <v-col>
      <v-data-table
        :headers="headers"
        :items="records"
        :items-per-page="5"
        class="elevation-1"
        :loading="loading"
        :search="search"
      >
        <template v-slot:top>
          <v-row>
            <v-col cols="3">
              <h2 class="mx-4">Plantillas</h2>
            </v-col>
            <v-col>
              <v-btn class="ma-2" small outlined color="primary" @click="$router.push('PlantillasTermRef/form')">
                nueva plantilla
                <v-icon right small>add</v-icon>
              </v-btn>
            </v-col>
            <v-col>
              <v-text-field
                v-model="search"
                append-icon="search"
                label="Filtrar"
                single-line
                hide-details
                class="mr-4"
              ></v-text-field>
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.acciones="{ item }">
          <v-btn icon color="primary" @click="$router.push('PlantillasTermRef/form/'+item.id)">
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn :loading="loading" icon color="primary" @click="droprecord(item)" class="ml-2">
            <v-icon>delete</v-icon>
          </v-btn>
          <v-btn :loading="loading" icon color="primary" @click="dialog = true, selected = item" class="ml-2">
            <v-icon>add</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-col>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">
          Clonar Plantilla:
          <br>
          {{ selected.nombre }}
        </v-card-title>
        <v-card-text>
            <v-text-field
              class="mt-5"
              label="Nombre"
              dense
              hide-details
              v-model="txtnombre"
              >
            </v-text-field>
      <v-select
        :items="tiposContratacion"
        label="Tipo contración"
        v-model="txttipoContratacion"
        class="mt-4"
        >
      </v-select>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="error darken-1"
            text
            @click="dialog = false, editItem = []"
          >
            Cerrar
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="clonerecord()"
            :loading="loading"
          >
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import { plantillas } from '@/servicios/PlantillasService.js'
import TiposContratacionService from '@/servicios/TiposContratacionService.js'
export default {
  data () {
    return {
      loading: false,
      headers: [
        { text: 'Id', value: 'id' },
        { text: 'Nombre Plantilla', value: 'nombre' },
        { text: 'Tipo Contratación', value: 'tipo_contratacion' },
        { text: 'Acciones', value: 'acciones' }
      ],
      records: [],
      tiposContratacion: [],
      dialog: false,
      txtnombre: '',
      txttipoContratacion: '',
      selected: '',
      search: ''
    }
  },
  mounted () {
    this.getplantillas()
    this.getTiposContracion()
  },
  methods: {
    async getplantillas() {
      this.loading = true
      try {
        const {data} = await plantillas.getPlantillas()
        this.records = data.termref_plantillas
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    async droprecord(item) {
      this.loading = true
      try {
        const {data} = await plantillas.dropPlantilla({ id: item.id })
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.getplantillas()
        } else if (data.tipo === 'error') {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    async clonerecord() {
      this.loading = true
      try {
        const {data} = await plantillas.clonePlantilla({ id: this.selected.id, nombre: this.txtnombre, tipoc: this.txttipoContratacion })
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.getplantillas()
          this.dialog = false
        } else if (data.tipo === 'error') {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    async getTiposContracion() {
      try {
       const result = await TiposContratacionService.getTiposForContratacion()
       this.tiposContratacion = result.data.tiposContratacion
      } catch (error) {
        console.error(error)
      }
    },
  }
}
</script>
